function mouseOver(properties) {
    if (properties.what === "background" && this.backgrounds[0]) {
        let test = this.backgrounds.find(background => background.start && background.end && properties.time && new Date(background.start) <= new Date(properties.time) && new Date(properties.time) <= new Date(background.end));
        if (test) {
            this.tooltipElement.style.visibility = "visible";
            this.tooltipElement.innerHTML = test.title;
            var rect = this.componentElement.getBoundingClientRect();
            var x = properties.event.clientX - rect.left;
            var y = properties.event.clientY - rect.top;
            if (x >= rect.width - 200) {
                x = x - 200;
            }
            this.tooltipElement.style.top = (y + 10) + 'px';
            this.tooltipElement.style.left = (x + 10) + 'px';
        }
        else {
            this.tooltipElement.style.visibility = "hidden";
            this.tooltipElement.innerHTML = "";
        }
    }
    else {
        this.tooltipElement.style.visibility = "hidden";
        this.tooltipElement.innerHTML = "";
    }
}
export { mouseOver };
