.s3pChartComponent .container-dateChange {
  display: flex;
  justify-content: center;
  margin: 10px;
  align-items: baseline !important;
}

/* input + label */
.s3pChartComponent .element-dateChange {
  display: flex;
  align-items: center;
  margin: 0px 5px;
}
.s3pChartComponent .element-dateChange label {
  margin-right: 5px;
}

/* container choice nb days */
.s3pChartComponent .containerRepeat {
  width: 330px;
  margin: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.s3pChartComponent .containerRepeat label {
  margin: 5px;
}
.s3pChartComponent .containerNbDays {
  display: flex;
  align-items: center;
  width: 330px;
}

/* btn week*/
.s3pChartComponent .containerWeekButton {
  display: flex;
  flex-wrap: wrap;
}
.s3pChartComponent .weekButton {
  margin: 5px;
  border: 2px solid var(--secondary-color, #1e49a5);
  padding: 5px;
  color: var(--secondary-color, #1e49a5);
  border-radius: 5px;
  text-align: center;
  flex: 1;
}
.s3pChartComponent .weekButton.selected {
  background-color: var(--secondary-color, #1e49a5) !important;
  color: white !important;
}

/* duration */
.s3pChartComponent .input-duration {
  width: 50px;
}

/* select */

.s3pChartComponent .container-dateChange .select-custom{
  max-width: 800px;
  flex: 1;
}