.tabsContainer {
  height: 100%;
}

.tabsContent .display {
  height: 100%;
}
.tabsContent {
  height: 100%;
}

/* first style */
.tabsFirstStyle {
  display: flex;
  justify-content: space-around;
  text-align: center;
}
.tabsFirstStyle .active {
  color: var(--primary-color, #921a1d) !important;
  border: var(--primary-color, #921a1d) solid 1px !important;
  border-bottom: 2.5px solid !important;
}
.tabsFirstStyle s3p-icon-v2 {
  display: inline-block;
  width: 1em;
  height: 1em;
  contain: strict;
  -webkit-box-sizing: content-box !important;
  box-sizing: content-box !important;
  font-size: 32px;
  overflow: hidden;
  padding: 7px;
}
.tabsFirstStyle .tab {
  width: 100%;
  font-size: 1em;
  border-radius: 4px;
  border: #89909e solid 1px;
  cursor: pointer;
}
.tabsFirstStyle .disabled {
  color: #cecdcd !important;
  border: #cecdcd solid 1px;
}

.tabsFirstStyle .blink {
  background: var(--primary-color, #921a1d) !important;
  border: var(--primary-color, #921a1d) solid 1px;
  animation: blinker 1s cubic-bezier(0.5, 0, 1, 1) infinite alternate;
}
/* seconde style */

.tabsSecondStyle {
  height: 6%;
  display: flex;
  padding-bottom: 7px;
}

.tabsSecondStyle .tab {
  flex: auto;
  border: none;
  color: grey;
  background: white;
  padding: 10px;
  cursor: pointer;
  outline: none;
  font-size: 0px;
  border-bottom: 3px solid transparent;
  font-weight: bolder;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}


.tabsSecondStyle .active {
  border-bottom: 3px solid var(--primary-color, #921a1d);
  color: var(--primary-color, #921a1d);
  font-size: 18px;
}

.tabsSecondStyle .blink {
  background: var(--primary-color, #921a1d) !important;
  color: white !important;
  animation: blinker 1s cubic-bezier(0.5, 0, 1, 1) infinite alternate;
}
@keyframes blinker {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

/* global*/
.no-display {
  display: none;
}
.display {
  display: block;
}
