.vis-item.other {
    background-color: var(--primary-custom-activity-color, #7f8c8d);
    border-color: var(--secondary-custom-activity-color, #424242);
}

.vis-item.work {
    background-color: var(--primary-work-color, #d0000d);
    border-color: var(--secondary-work-color, #b92602);
}

.vis-item.available {
    background-color: var(--primary-available-color, #e67d20);
    border-color: var(--secondary-available-color, #c25c03);
}

.vis-item.drive {
    background-color: var(--primary-drive-color, #01599d);
    border-color: var(--secondary-drive-color, #023d69);
}

.vis-item.rest {
    background-color: var(--primary-rest-color, #2f7f33);
    border-color: var(--secondary-rest-color, #16721b);
}

.vis-item.rest15m {
    background: linear-gradient(90deg, var(--light-rest-color, #67ac6a) 50%, var(--primary-rest-color, #2f7f33) 50%);
    background-size: 10px 10px;
    border-color: var(--secondary-rest-color, #16721b);
}

.vis-item.rest45m {
    background: linear-gradient(
        135deg,
        var(--light-rest-color, #67ac6a) 25%,
        var(--primary-rest-color, #2f7f33) 25%,
        var(--primary-rest-color, #2f7f33) 50%,
        var(--light-rest-color, #67ac6a) 50%,
        var(--light-rest-color, #67ac6a) 75%,
        var(--primary-rest-color, #2f7f33) 75%
    );
    background-size: 10px 10px;
    border-color: var(--secondary-rest-color, #16721b);
}

.vis-item.rest9h {
    background: linear-gradient(
        180deg,
        var(--light-rest-color, #67ac6a) 25%,
        var(--primary-rest-color, #2f7f33) 25%,
        var(--primary-rest-color, #2f7f33) 50%,
        var(--light-rest-color, #67ac6a) 50%,
        var(--light-rest-color, #67ac6a) 75%,
        var(--primary-rest-color, #2f7f33) 75%
    );
    background-size: 10px 10px;
    background-color: var(--primary-rest-color, #2f7f33);
    border-color: var(--secondary-rest-color, #16721b);
}

.vis-item.resource-odd {
    height: 5px;
    background: rgb(246, 234, 54);
}

.vis-item.resource-even {
    height: 5px;
    background: rgb(165, 49, 241);
}

.vis-item.start,
.vis-item.end {
    color: var(--primary-color, #921a1d);
    background-color: white;
    font-size: 22px;
    z-index: 2;
    border-color: var(--primary-color-shade, #7a1a1e);
}

.vis-item.start-green,
.vis-item.end-green {
    color: green;
