function setPeriodFlags(periods, lastLegalFileEvent) {
    let periodFlags = [];
    for (let period of periods) {
        if (period.start && period.start.ts) {
            let start = period.start;
            const className = lastLegalFileEvent && new Date(lastLegalFileEvent) >= new Date(period.start.ts) ? "start-green" : "start";
            const classIcon = lastLegalFileEvent && new Date(lastLegalFileEvent) >= new Date(period.start.ts) ? "icon-timeline-green" : "icon-timeline";
            periodFlags.push({
                content: `<div class="periodTextParent">
            <svg class="${classIcon}" aria-hidden="true" ><use xlink:href="#play"></use></svg>` +
                    (this.annotated ? `<div class="periodTextStart">${this.prettyDate(new Date(start.ts), "00/00 00:00")}</div>` : '') +
                    `</div>`,
                start: new Date(period.start.ts),
                group: "start",
                ///   subgroup: "periods",
                editable: false,
                latLng: start.geo && start.geo.coordinates ? start.geo.coordinates : null,
                className,
                title: ` ${this.i18n.get('serviceBeginning')} ${this.prettyDate(new Date(start.ts), "00/00/0000 00:00")} ${start.address && (start.address.postalcode || start.address.name) ? `</br> ${this.i18n.get('in')} : ${start.address.postalcode ? start.address.postalcode : ""} ${start.address.name ? start.address.name : ""}` : ""}`,
                period: period
            });
        }
        if (period.end && period.end.ts) {
            let end = period.end;
            const className = lastLegalFileEvent && new Date(lastLegalFileEvent) >= new Date(period.end.ts) ? "end-green" : "end";
            const classIcon = lastLegalFileEvent && new Date(lastLegalFileEvent) >= new Date(period.end.ts) ? "icon-timeline-green" : "icon-timeline";
            periodFlags.push({
                content: `<div class="periodTextParent">
        <svg class="${classIcon}" aria-hidden="true" ><use xlink:href="#arrival"></use></svg>` +
                    (this.annotated ? `<div class="periodTextStop">${this.prettyDate(new Date(end.ts), "00/00 00:00")}</div>` : '') +
                    `</div>`,
                start: new Date(period.end.ts),
                group: "start",
                editable: false,
                //  subgroup: "periods",
                className,
                title: `${this.i18n.get('serviceEnding')} ${this.prettyDate(new Date(end.ts), "00/00/0000 00:00")} ${end.address && (end.address.postalcode || end.address.name) ? `</br> ${this.i18n.get('in')} : ${end.address.postalcode ? end.address.postalcode : ""} ${end.address.name ? end.address.name : ""}` : ""}`,
                period: period
            });
        }
    }
    return periodFlags;
}
export { setPeriodFlags };
