function click(properties) {
    if (properties.item && this.items.get(properties.item).event && this.items.get(properties.item).event.activity !== 'D') {
        this.clickOnTimeline.emit({
            event: this.items.get(properties.item).event,
            // nextEvent: this.items.get(properties.item).nextEvent,
            start: this.items.get(properties.item).start,
            end: this.items.get(properties.item).end
        });
    }
    else if (properties.item && this.items.get(properties.item).event && this.items.get(properties.item).nextEvent && this.items.get(properties.item).event.activity === 'D') {
        this.clickOnTimeline.emit({
            event: this.items.get(properties.item).event,
            nextEvent: this.items.get(properties.item).nextEvent,
            start: this.items.get(properties.item).start,
            end: this.items.get(properties.item).end
        });
    }
    else if (properties.item && this.items.get(properties.item).period) {
        this.clickOnTimeline.emit({
            period: this.items.get(properties.item).period,
            start: this.items.get(properties.item).start,
        });
    }
}
export { click };
