            if (this.pairVehicleDriver) {
                dataSet = dataSet.concat(this.setPeriodResources(this.pairVehicleDriver, this.resourceType));
            }
            let tsBar;
            if (this.display_start && this.display_end) {
                tsBar = (new Date(this.display_end).valueOf() + new Date(this.display_start).valueOf()) / 2;
            }
            if (dataSet[0] && dataSet[0].start && dataSet[dataSet.length - 1] && dataSet[dataSet.length - 1].end) {
                if (!tsBar) {
                    tsBar = (new Date(dataSet[0].start).valueOf() + new Date(dataSet[dataSet.length - 1].end).valueOf()) / 2;
                }
            }
            if (this.periodFlags) {
                dataSet = dataSet.concat(this.periodFlags);
            }
            if (this.lastCommunicationsItems) {
                dataSet = dataSet.concat(this.lastCommunicationsItems);
            }
            this.dataSet = dataSet;
            this.items = new vis.DataSet(dataSet);
            this.setStartAndEndOnTimeline();
            this.timeline.setItems(this.items);
            this.log('data in initialization', dataSet);
