    setMilleageDiff(start, end) {
        let eventsAsc = this.events;
        eventsAsc.sort((a, b) => {
            if (new Date(a.ts).valueOf() < new Date(b.ts).valueOf()) {
                return -1;
            }
            if (new Date(a.ts).valueOf() > new Date(b.ts).valueOf()) {
                return 1;
            }
            return 0;
        });
        let eventStart, eventEnd;
        let duranteStart, duranteEnd;
        for (let i of Object.keys(eventsAsc)) {
            const duranteStartTemp = Math.abs(new Date(eventsAsc[i].ts).valueOf() - new Date(start).valueOf());
            const duranteEndTemp = Math.abs(new Date(eventsAsc[i].ts).valueOf() - new Date(end).valueOf());
            if (eventsAsc[i].mileage && (duranteStart == null || duranteStart > duranteStartTemp)) {
                duranteStart = Math.abs(new Date(eventsAsc[i].ts).valueOf() - new Date(start).valueOf());
                eventStart = eventsAsc[i];
            }
            if (eventsAsc[i].mileage && (duranteEnd == null || duranteEnd > duranteEndTemp)) {
                duranteEnd = Math.abs(new Date(eventsAsc[i].ts).valueOf() - new Date(end).valueOf());
                eventEnd = eventsAsc[i];
            }
        }
        let result;
        if (eventEnd && eventEnd.mileage && eventStart && eventStart.mileage) {
            result = eventEnd.mileage - eventStart.mileage;
            result = (Math.round(result * 100) / 100).toString() + "km";
        }
        this.log('mileage in setMilleageDiff', result);
        this.log('eventStart in setMilleageDiff', eventStart);
        this.log('eventEnd in setMilleageDiff', eventEnd);
        return { diff: result, start: eventStart, end: eventEnd };
    }
    setIntervalBar(start, end) {
        this.log('start in setIntervalBar', start);
        this.log('end in setIntervalBar', end);
        const result = this.setMilleageDiff(start, end);
        return ({
            id: 'intervalBarItem', content: result && result.diff ? result.diff : '???', className: "intervalBar", start: result && result.start && result.start.ts ? result.start.ts : start, end: result && result.end && result.end.ts ? result.end.ts : end, group: 'start'
        });
    }
    setCustomBar(start) {
        if (this.haveCustomBar) {
            try {
                this.timeline.setCustomTime(new Date((start)), "customTimeEvent");
            }
            catch (error) {
                this.timeline.addCustomTime(new Date(start), "customTimeEvent");
            }
            this.timeline.on('timechange', (properties) => {
                let currentEvent;
                for (let index of Object.keys(this.dataSet)) {
                    let event = this.dataSet[index];
                    if (event.start < properties.time && event.end > properties.time && event.event && event.event.activity && event.event.activity !== 'D') {
                        currentEvent = event;
                    }
                }
                if (currentEvent) {
                    this.moveCustomBar.emit(properties.time);
                }
            });
            this.timeline.on('timechanged', (properties) => {
                //  console.log(properties.time)
                this.changeCustomBar.emit(properties.time);
            });
        }
        else {
            try {
                this.timeline.removeCustomTime("customTimeEvent");
            }
            catch (error) { }
        }
    }
