    /**
     * Methode pour changer l'attribut stack
     * @param stack:boolean
     */
    async setStack(stack) {
        let range = this.timeline.getWindow();
        this.stack = stack;
        this.options.stackSubgroups = stack;
        let groupsName = ["all", "bar"];
        for (let group of groupsName) {
            this.groups.update({
                id: group,
                visible: !this.groups.get(group).visible
            });
        }
        this.options["start"] = new Date(range.start);
        this.options["end"] = new Date(range.end);
        this.timeline.setGroups(this.groups);
        this.timeline.setOptions(this.options);
        this.timeline.redraw();
        localStorage.setItem("timeGroup", `${this.stack}`);
        this.stackChange.emit(this.stack);
    }
