function setNotes(notes) {
    let items = [];
    for (let index = 0; index < notes.length; index++) {
        const note = notes[index];
        let start = new Date(note.ts);
        items.push({
            id: 'note-' + index,
            className: 'note-' + index % 2,
            group: 'start',
            start: start,
            content: (this.annotated ? note.text : ''),
            type: 'point',
            title: note.text + ' ' + this.prettyDate(start, "00:00")
        });
    }
    return items;
}
export { setNotes };
