function rangechanged(properties, periods) {
    this.log('rangechanged', properties.start);
    this.log('rangechanged', properties.end);
    this.log('periods', periods);
    const vehicles = {};
    // Sort events by vehicle
    for (const event of this.events) {
        if (event.mileage || event.totalFuel) {
            if (!vehicles[event.vehicleId]) {
                vehicles[event.vehicleId] = [];
            }
            vehicles[event.vehicleId].push(event);
        }
    }
    let firstActivity, lastActivity, drive = 0, available = 0, work = 0, rest = 0;
    // Calculate the driver activity time
    for (let data of this.dataSet) {
        if (data.start && data.end && data.start >= properties.start && data.end <= properties.end && data.group === "bar") {
            if (!firstActivity) {
                firstActivity = data.event;
            }
            lastActivity = data.nextEvent ? data.nextEvent : lastActivity;
            if (data.event && data.event.activity) {
                if (data.event.activity === 'D') {
                    drive += data.end.valueOf() - data.start.valueOf();
                }
                if (data.event.activity === 'A') {
                    available += data.end.valueOf() - data.start.valueOf();
                }
                if (data.event.activity === 'W') {
                    work += data.end.valueOf() - data.start.valueOf();
                }
                if (data.event.activity === 'R') {
                    rest += data.end.valueOf() - data.start.valueOf();
                }
            }
        }
    }
    let periodList = [];
    if (this.resourceType === "driver") {
        // Find all periods and adapt the start/end to the timeline properties
        if (periods && periods.length) {
            for (const period of periods) {
                let needToPush = false;
                let periodStart = null;
                let periodEnd = null;
                if (period.start && period.start.ts) {
                    // Take the end.ts or lastEvent.ts if period is finished or not
                    if (period.end && period.end.ts) {
                        periodEnd = period.end.ts;
                    }
                    else {
                        if (period.lastEvent && period.lastEvent.ts) {
                            periodEnd = period.lastEvent.ts;
                        }
                    }
                    // If the period start is between timeline start and from
                    if (new Date(period.start.ts).getTime() >= new Date(properties.start).getTime() &&
                        new Date(period.start.ts).getTime() < new Date(properties.end).getTime()) {
                        periodStart = period.start.ts;
                        if (periodEnd) {
                            // If the period end is after the timeline end, take the properties.end
                            if (new Date(periodEnd).getTime() > new Date(properties.end).getTime()) {
                                periodEnd = new Date(properties.end);
                            }
                            needToPush = true;
                        }
                    }
                    else {
                        // If period start is before the timeline start
                        if (new Date(period.start.ts).getTime() < new Date(properties.start).getTime()) {
                            // If the period end is between the timeline start and end, take the timeline start as period start
                            if (periodEnd && new Date(periodEnd).getTime() > new Date(properties.start).getTime()) {
                                // If the period end is after the timeline end, take the properties.end
                                if (new Date(periodEnd).getTime() > new Date(properties.end).getTime()) {
                                    periodEnd = new Date(properties.end);
                                }
                                periodStart = new Date(properties.start);
                                needToPush = true;
                            }
                        }
                    }
                }
                if (needToPush) {
                    periodList.push({ start: periodStart, end: periodEnd });
                }
            }
        }
    }
    else {
        periodList.push({ start: new Date(properties.start), end: new Date(properties.end) });
    }
    this.log('periodList: ', periodList);
    let mileage = 0, totalFuel = 0;
    // For every vehicle and every period, search the first and last mileage/totalFuel
    for (const vehicleId of Object.keys(vehicles)) {
        // Total by vehicle
        let vehicleTotalMileage = 0, vehicleTotalFuel = 0;
        // Check every period
        for (const period of periodList) {
            let firstMileageEvent = null, lastMileageEvent = null, firstFuelEvent = null, lastFuelEvent = null;
            let periodTotalMileage = 0;
            let periodTotalFuel = 0;
            for (const event of vehicles[vehicleId]) {
                if ((event.mileage || event.totalFuel) &&
                    new Date(event.ts).getTime() >= new Date(period.start).getTime() &&
                    new Date(event.ts).getTime() <= new Date(period.end).getTime()) {
                    if (event.mileage) {
                        if (!firstMileageEvent) {
                            firstMileageEvent = event;
                        }
                        else {
                            if (new Date(firstMileageEvent.ts).getTime() > new Date(event.ts).getTime()) {
                                firstMileageEvent = event;
                            }
                        }
                        if (!lastMileageEvent) {
                            lastMileageEvent = event;
                        }
                        else {
                            if (new Date(lastMileageEvent.ts).getTime() < new Date(event.ts).getTime()) {
                                lastMileageEvent = event;
                            }
                        }
                    }
                    if (event.totalFuel) {
                        if (!firstFuelEvent) {
                            firstFuelEvent = event;
                        }
                        else {
                            if (new Date(firstFuelEvent.ts).getTime() > new Date(event.ts).getTime()) {
                                firstFuelEvent = event;
                            }
                        }
                        if (!lastFuelEvent) {
                            lastFuelEvent = event;
                        }
                        else {
                            if (new Date(lastFuelEvent.ts).getTime() < new Date(event.ts).getTime()) {
                                lastFuelEvent = event;
                            }
                        }
                    }
                }
            }
            // Calculate totals for the period
            if (firstMileageEvent && lastMileageEvent) {
                periodTotalMileage = lastMileageEvent.mileage - firstMileageEvent.mileage;
            }
            if (firstFuelEvent && lastFuelEvent) {
                periodTotalFuel = lastFuelEvent.totalFuel - firstFuelEvent.totalFuel;
            }
            // Add period totals to the vehicle totals
            vehicleTotalMileage += periodTotalMileage;
            vehicleTotalFuel += periodTotalFuel;
        }
        // Add all vehiclee totals
        mileage += vehicleTotalMileage;
        totalFuel += vehicleTotalFuel;
    }
    this.log('Total mileage: ', Math.round(mileage * 100) / 100);
    this.log('Total fuel: ', Math.round(totalFuel * 1000) / 1000);
    this.startAndEndChange.emit({
        start: properties.start, end: properties.end,
        firstActivity: firstActivity, lastActivity: lastActivity,
        available: available / 1000, drive: drive / 1000,
        work: work / 1000, rest: rest / 1000,
        firstLoad: this.firstLoad,
        mileage: Math.round(mileage * 100) / 100,
        totalFuel: Math.round(totalFuel * 1000) / 1000 // round to 3 decimals
    });
    this.firstLoad = false;
}
export { rangechanged };
